.footer {
  min-height: 10px;
  background: #E5E5E5;
  position: relative;
  color: gray;
  text-align: center;

}

.footer span {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 15px;
  background: lightskyblue;
}

.wrapper {
  min-height: 100%;
  padding-bottom: 100px;
}

.hero {
  height: 300px;
  background-size: cover;
  background-position: center;
  position: relative;
}

.hero::after {
  content: "";
  display: block;
  opacity: .4;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgb(141, 98, 41);
}

.hero * {
  position: relative;
  z-index: 2;
}

.hero h1, .hero h2 {
  color: gray;
}

.hero h1 {
  font-size: 40px;
  padding-top: 120px;
  margin-top: 0;
}

nav.navbar.navbar-default {
  margin-bottom: 0;

}
