.card {
  background-size: cover;
  background-position: center;
  background-clip: content-box;
  /* border: 6px solid white; */
  height: 150px;
  width: 150px;
  box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
  position: relative;
  margin: 10px;
  margin-top: 30px;
  background-repeat: no-repeat;
  float: left;
}

.card .fa-spinner {
  font-size: 180px;
  position: absolute;
  left: 25%;
  top: 25%;
}

.card:hover{
  -webkit-transform:scale(1.1);
          transform:scale(1.1);
  cursor: pointer;
}
* {
  box-sizing: border-box;
}

html,
body,
#root,
#root > div,
div[data-reactroot] {
  height: 100%;
  padding: 0;
  margin: 0;
}

